<template>
	<vx-card class="">
		<form-wizard color="rgba(var(--vs-primary), 1)" :title="null" nextButtonText="Siguiente" backButtonText="Atrs" finishButtonText="Crear Campaa" :subtitle="null" @on-complete="formSubmitted">
			<tab-content title="Cargar Usuarias" class="mb-5" :before-change="validarCargarUsuarias">

				<!-- Tabla 1 Cargar Usuarios ------------------------------------------------------ -->
				<div class="vx-row" style="padding-top:30px">
					<!-- Cargar nuevas usuarias -->
					<div class="vx-col w-full sm:w-1/3 md:w-1/3 lg:w-1/3 xl:w-1/3 mb-base">
						<div>
							<import-excel :onSuccess="loadDataInTable"/>
						</div>
					</div>
					<!-- Contador de usuarias cargadas -->
					<div class="vx-col w-full sm:w-1/3 md:w-1/3 lg:w-1/3 xl:w-1/3 mb-base">
						<div class="vx-card p-6">
							<center>
								<span class="p-4 inline-flex rounded-full feather-icon select-none relative text-primary mb-4" style="background: rgba(var(--vs-primary),0.15);">
									<vs-icon icon="groups" size="25px"></vs-icon>
								</span>
								<h2 class="mb-1 font-bold">{{ campanaNueva.cantidadUsuarias }}</h2>
								<p>Usuarias Cargadas</p>
								<div v-if="campanaNueva.nombre != ''" class="pt-5">
									<span class="p-4 inline-flex rounded-full feather-icon select-none relative text-primary mb-4" style="background: rgba(var(--vs-primary),0.15);">
										<vs-icon icon="favorite" size="25px"></vs-icon>
									</span>
									<p>Nombre de la Campaa</p>
									<h4 class="mb-1 font-bold">{{campanaNueva.nombre}}</h4>
								</div>
							</center>
						</div>
					</div>
					<!-- Seleccionar Usuarias -->
					<div class="vx-col w-full sm:w-1/3 md:w-1/3 lg:w-1/3 xl:w-1/3 mb-base">
						<div class="vx-card p-6">
							<center>
								<span class="p-4 inline-flex rounded-full feather-icon select-none relative text-primary mb-4" style="background: rgba(var(--vs-primary),0.15);">
									<vs-icon icon="playlist_add_check" size="35px"></vs-icon>
								</span>
								<h2 class="mb-1 font-bold">Seleccionar Usuarias Existentes</h2>
							</center>
						</div>
					</div>
				</div>
				<!-- Modal de Cargar Usuarias en Excel -->
				<vs-popup fullscreen title="Cargar Usuarias" :active.sync="modalExcel">
					<vx-card v-if="tableData.length && header.length">
						<div class="vx-row" style="padding-top:0px">
							<!-- Nombre de la nueva Base de Datos de usuarias-------------- -->
							<div class="vx-col w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2 mb-base">
								<vx-input-group class="mb-base">
									<template slot="prepend">
									<div class="prepend-text bg-primary">
										<span>Nombre de la nueva lista de usuarias: </span>
									</div>
									</template>
									<vs-input v-model="campanaNueva.nombreDB" placeholder="Escribe el nombre de la nueva lista de usuarias" />
								</vx-input-group>
							</div>
							<!-- Nombre de la Campaa ------ -->
							<div class="vx-col w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2 mb-base">
								<vx-input-group class="mb-base">
									<template slot="prepend">
									<div class="prepend-text bg-primary">
										<span>Nombre de la Campaa: </span>
									</div>
									</template>
									<vs-input v-model="campanaNueva.nombre" placeholder="Escribe como se llamar esta nueva campaa" />
								</vx-input-group>
							</div>
						</div>
						<!-- Botones de confirmar -->
						<center>
							<span class="inline-flex relative">
								<span style="padding-right:20px">
									<vs-button color="danger" type="filled" icon-pack="feather" icon="icon-x" @click="cancelarSubidaUsuarias()">Cancelar</vs-button>	
								</span>
								<span style="padding-right:20px">
									<vs-button color="success" type="filled" icon-pack="feather" icon="icon-check" @click="cargarSubidaUsuarias()">Cargar Usuarias</vs-button>
								</span>
							</span>
						</center>
						<!-- Tabla de datos del Excel -->
						<vs-table stripe pagination :max-items="10" search :data="tableData">
							<template slot="header">
							<h4>{{ sheetName }}</h4>
							</template>
							<template slot="thead">
							<vs-th :sort-key="heading" v-for="heading in header" :key="heading">{{ heading }}</vs-th>
							</template>
							<template slot-scope="{data}">
							<vs-tr :data="tr" :key="indextr" v-for="(tr, indextr) in data">
								<vs-td :data="col" v-for="(col, indexcol) in data[indextr]" :key="indexcol + col">
								{{ col }}
								</vs-td>
							</vs-tr>
							</template>
						</vs-table>
    				</vx-card>
				</vs-popup>
			</tab-content>

			<!-- Tabla 2 Seleccionar Medios, Intensidad y Duracin ---------------------------------- -->
			<tab-content title="Seleccionar Medios, Intensidad y Duracin" class="mb-5" :before-change="validarSeleccionarMedios">
				<!-- Fila 1 Duracin en Das y Medios Seleccionados -->
				<div class="vx-row" style="padding-top:30px">
					<div class="vx-col w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2 mb-base">
						<statistics-card-line style="width:200px; float: right;"
						hideChart
						class="mb-base"
						icon="UsersIcon"
						:statistic="duracionEnDias()"
						statisticTitle="Duracin en Das" 
						color="primary" />	
					</div>
					<div class="vx-col w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2 mb-base">
						<vx-card style="width:200px">
							<center>
								<span class="p-3 inline-flex rounded-full feather-icon select-none relative text-primary mb-4" style="background: rgba(var(--vs-primary),0.15);">
									<vs-icon icon="notifications_active" size="26px"></vs-icon>
								</span>
								<div>
									<span v-for="item in campanaNueva.medios" :key="item.id">
										<IconifyIcon v-if="item.id =='1'" :icon="iconos.CelularIcon" width="25" />
										<IconifyIcon v-else-if="item.id =='2'" :icon="iconos.TelefonoIcon" width="25" />
										<IconifyIcon v-else-if="item.id =='3'" :icon="iconos.EmailIcon" width="25" />
										<IconifyIcon v-else-if="item.id =='4'" :icon="iconos.SMSIcon" width="25" />
										<IconifyIcon v-else-if="item.id =='5'" :icon="iconos.WhatsappIcon" width="25" />
									</span>
								</div>
								<p>Medios Seleccionados</p>
							</center>
						</vx-card>
					</div>
				</div>
				<!-- Fila 2 Fechas y Medios -->
				<div class="vx-row">
					<!-- Primera Tarjeta de Fechas de Inicio y Fin -->
					<div class="vx-col w-full sm:w-1/3 md:w-1/3 lg:w-1/3 xl:w-1/3 mb-base">
						<vx-card class="">
							<center>
								<p><strong>Fecha de Inicio</strong></p><br>
								<flat-pickr :config="configFechaInicio" v-model="campanaNueva.fechaInicio" @on-change="onFromChange" />
								<p></p><br><br>
								<p><strong>Fecha de Final</strong></p><br>
								<flat-pickr :config="configFechaFin" v-model="campanaNueva.fechaFin" @on-change="onToChange" />
							</center>
						</vx-card>
					</div>
					<div class="vx-col w-full sm:w-2/3 md:w-2/3 lg:w-2/3 xl:w-2/3 mb-base">
					<!-- Segunda Tarjeta de Seleccionar Medios -->
						<vx-card class="">
							<!-- Titulos de Seleccionar Medios -->
							<div class="vx-row">
								<div class="vx-col w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2 mb-base">	
									<center>
										<p><strong>Seleccin de Medios</strong></p>
									</center>
								</div>
								<div class="vx-col w-full sm:w-1/4 md:w-1/4 lg:w-1/4 xl:w-1/4 mb-base">
									<center>
										<p><strong>Intensidad Diaria</strong></p>
									</center>
								</div>
								<div class="vx-col w-full sm:w-1/4 md:w-1/4 lg:w-1/4 xl:w-1/4 mb-base">
									<center>
										<p><strong>Hora</strong></p>
									</center>
								</div>
							</div>
							<!-- Todos los medios -->
							<div v-for="item in medios" :key="item.id" class="vx-row" style="margin-top:-25px;">
								<div class="vx-col w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2 mb-base">
									<div @click="item.activo = !item.activo; item.intensidad = 1">
									<vs-checkbox v-model="campanaNueva.medios" :vs-value="item">
										<IconifyIcon v-if="item.id =='1'" :icon="iconos.CelularIcon" width="25" />
										<IconifyIcon v-if="item.id =='2'" :icon="iconos.TelefonoIcon" width="25" />
										<IconifyIcon v-if="item.id =='3'" :icon="iconos.EmailIcon" width="25" />
										<IconifyIcon v-if="item.id =='4'" :icon="iconos.SMSIcon" width="25" />
										<IconifyIcon v-if="item.id =='5'" :icon="iconos.WhatsappIcon" width="25" />
										{{ item.nombre }}
									</vs-checkbox>
									</div>
								</div>
								<div class="vx-col w-full sm:w-1/4 md:w-1/4 lg:w-1/4 xl:w-1/4 mb-base">
									<vs-input-number v-model="item.intensidad" min="1" max="5" :disabled="!item.activo" icon-inc="expand_less" icon-dec="expand_more"/>
								</div>
								<div class="vx-col w-full sm:w-1/4 md:w-1/4 lg:w-1/4 xl:w-1/4 mb-base">
									<center>
										<flat-pickr :config="configHora" class="h" :disabled="!item.activo" v-model="item.hora" placeholder="00:00:00" />
									</center>
								</div>
							</div>
						</vx-card>
					</div>
				</div>
			</tab-content>

			<!-- Tabla 3 Configurar Medios -->
			<tab-content title="Configurar Medios" class="mb-5" :before-change="validarConfigurarMedios">
				<div class="vx-row" style="padding-top:10px;">
					<div class="vx-col w-full" v-for="item in campanaNueva.medios" :key="item.id" >
						<div style="padding-top:20px; padding-bottom:20px">
							<vx-card>
								<!-- Titulo de la configuracin de cada medio -->
								<span class="inline-flex relative">
									<IconifyIcon v-if="item.id =='1'" :icon="iconos.CelularIcon" width="25" />
									<IconifyIcon v-else-if="item.id =='2'" :icon="iconos.TelefonoIcon" width="25" />
									<IconifyIcon v-else-if="item.id =='3'" :icon="iconos.EmailIcon" width="25" />
									<IconifyIcon v-else-if="item.id =='4'" :icon="iconos.SMSIcon" width="25" />
									<IconifyIcon v-else-if="item.id =='5'" :icon="iconos.WhatsappIcon" width="25" />
									<h3 style="padding-left:15px; padding-top:5px"> <strong> Configurar {{ item.nombre }}</strong></h3>
								</span>
								<p style="padding-left:38px;padding-top:10px">{{ item.descripcionConfig }}</p>
								<!-- SI es una llamada celular o telefnica para cargar el MP3 ---------- -->
								<center>
									<vs-upload v-if="item.id=='1' || item.id=='2'" :action="item.urlPostMP3"
										style="width:240px; float: center;"
										limit=1
										text="Click para cargar MP3"
										accept=".mp3"
										@on-success="correctoCargarMP3" 
										@on-error="errorCargarMP3"/>
								</center>
								<!-- Si es un correo electrnico -->
								<div v-if="item.id=='3'" class="vx-row" style="padding-left:35px; padding-top:20px; padding-right:50px;">
									<!-- Asunto -------------- -->
									<div class="vx-col w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2 mb-base">
										<vx-input-group class="mb-base">
											<template slot="prepend">
											<div class="prepend-text bg-primary">
												<span>Asunto: </span>
											</div>
											</template>
											<vs-input v-model="item.asunto" placeholder="Asunto del Correo Electrnico" />
										</vx-input-group>
									</div>
									<!-- Correo Emisor ------ -->
									<div class="vx-col w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2 mb-base">
										<vx-input-group class="mb-base">
											<vs-input v-model="item.correoEmisor" placeholder="Correo Emisor" />
											<template slot="append">
											<div class="append-text bg-primary">
												<span>@saludpublica.com</span>
											</div>
											</template>
										</vx-input-group>
									</div>
									<!-- Cuerpo del Mensaje ----- -->
									<div class="vx-col w-full" style="margin-top:-30px">
										<quill-editor v-model="item.cuerpoMensaje"></quill-editor>
									</div>
								</div>
								<!-- Si es un mensaje de WhatsApp -->
								<div class="vx-col w-full" style="padding-left:35px; padding-top:30px; padding-right:50px;">
									<vs-textarea v-if="item.id=='4' || item.id =='5'" counter="500" 
										:label="'Cuerpo del '+ item.nombre +'. Mximo: 500 Caracteres'" 
										:counter-danger.sync="item.alertaContador" v-model="item.mensaje" />
								</div>
							</vx-card>
						</div>
					</div>
				</div>
			</tab-content>
		</form-wizard>
	</vx-card>
</template>

<script>
import {FormWizard, TabContent} from 'vue-form-wizard'
import 'vue-form-wizard/dist/vue-form-wizard.min.css'
import StatisticsCardLine from '@/components/statistics-cards/StatisticsCardLine.vue'
import flatPickr from 'vue-flatpickr-component';
import 'flatpickr/dist/flatpickr.css';
import IconifyIcon from '@iconify/vue';
import moment from 'moment';
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'
import CelularIcon from '@iconify/icons-ic/baseline-local-phone';
import TelefonoIcon from '@iconify/icons-entypo/old-phone';
import EmailIcon from '@iconify/icons-ic/round-email';
import SMSIcon from '@iconify/icons-ic/baseline-sms';
import WhatsappIcon from '@iconify/icons-dashicons/whatsapp';
import ImportExcel from '@/components/excel/ImportExcel.vue';
import isvalid from 'isvalid';
import paperwork from 'paperwork';


export default {
	data() {
		return {
			iconos: {
				CelularIcon,
				TelefonoIcon,
				EmailIcon,
				SMSIcon,
				WhatsappIcon,
			},
			campanaNueva: {
				nombre: '',
				cantidadUsuarias: 0,
				fechaInicio: new Date(),
				fechaFin: new Date(),
				duracionTotal: 1,
				nombreDB:'',
				medios: [],
			},
			medios:[
				{
					id: "1",
					activo: false,
					nombre: "Llamada al Celular",
					intensidad: 0,
					hora: "00:00:00",
					descripcionConfig: "Carga el audio que deseas reproducir al momento en que las usuarias contesten las llamadas. Slo formato mp3", 
					urlPostMP3: "https://jsonplaceholder.typicode.com/posts/",
				},
				{
					id: "2",
					activo: false,
					nombre: "Llamada al Telfono Fijo",
					intensidad: 0,
					hora: "00:00:00",
					descripcionConfig: "Carga el audio que deseas reproducir al momento en que las usuarias contesten las llamadas. Slo formato mp3",
					urlPostMP3: "https://jsonplaceholder.typicode.com/posts/",
				},
				{
					id: "3",
					activo: false,
					nombre: "Correo Electrnico",
					intensidad: 0,
					hora: "00:00:00",
					descripcionConfig: "Configura los siguientes datos para poder enviar los correos electrnicos a las usuarias",
					asunto: '',
					correoEmisor: '',
					cuerpoMensaje: `Escribe el Cuerpo del Correo Electrnico...`,
				},
				{
					id: "4",
					activo: false,
					nombre: "Mensaje de Texto SMS",
					intensidad: 0,
					hora: "00:00:00",
					descripcionConfig: "Configura el cuerpo del mensaje de texto que le llegar a las usuarias",
					mensaje: '',
					alertaContador: false,
				},
				{
					id: "5",
					activo: false,
					nombre: "Mensaje al WhatsApp",
					intensidad: 0,
					hora: "00:00:00",
					descripcionConfig: "Configura el cuerpo del mensaje de texto que le llegar a las usuarias",
					mensaje: '',
					alertaContador: false,
				}
			],
            configFechaInicio: {
				minDate: new Date(),
				maxDate: null,
				dateFormat: 'd-m-Y',
            },
            configFechaFin: {
				minDate: new Date(),
				dateFormat: 'd-m-Y',
            },
			configHora: {
				enableTime: true,
				enableSeconds: true,
				noCalendar: true,
				time_24hr: false,
			},
			validacionHeaderExcel: ["identidad", "nombre", "fecha_nacimiento", "celular", "telefono", "email"],
			modalExcel: false,
			tableData: [],
			header: [],
			sheetName: '',
		}
	},
	methods: {
		validarCargarUsuarias() {
			return new Promise((resolve, reject) => {
				if (this.campanaNueva.cantidadUsuarias == 0 || this.campanaNueva.nombre== '' || this.campanaNueva.nombreDB == ''){
					reject("correct all values");
					this.alerta('danger', 'Error Seleccionar Lista de usuarias', 'Para poder continuar debes cargar o seleccionar una lista de usuarias vlida, y asignarle un nombre a la campaa');
				}else{
					resolve(true);
				}
			})
		},
		validarSeleccionarMedios() {
			return new Promise((resolve, reject) => {
				if (!this.campanaNueva.medios.length){
					reject("correct all values");
					this.alerta('danger', 'Error al Seleccionar Medios', 'Para poder continuar debes Seleccionar por lo menos un medio');
				}else{
					resolve(true);
				}
			})
		},
		validarConfigurarMedios() {
			var error = false;
			for (var i in this.campanaNueva.medios){
				if(this.campanaNueva.medios[i].id == '1' || this.campanaNueva.medios[i].id == '2'){
					//Falta completar lo de verificar si si carg el audio
				}
				else if(this.campanaNueva.medios[i].id == '3'){
					if(this.campanaNueva.medios[i].asunto == '' || this.campanaNueva.medios[i].correoEmisor == '' || this.campanaNueva.medios[i].cuerpoMensaje == ''){
						error = true;
					}
				}
				else if(this.campanaNueva.medios[i].id == '4' || this.campanaNueva.medios[i].id == '5'){
					if(this.campanaNueva.medios[i].mensaje == ''){
						error = true;
					}
				}
			}
			return new Promise((resolve, reject) => {
				if (error){
					reject("correct all values");
					this.alerta('danger', 'Error al configurar medios', 'Para crear la nueva campaa debes completar todos los campos');
				}else{
					resolve(true);
				}
			})
		},
		duracionEnDias(){
			this.duracionTotal = moment(this.campanaNueva.fechaFin, 'DD-MM-YYYY').diff(moment(this.campanaNueva.fechaInicio, 'DD-MM-YYYY'), 'days')+1 ;
			return this.duracionTotal;
		},
		formSubmitted() {
			this.alerta('success', 'Campaa creada correctamente', 'Hemos creado tu nueva campaana, puedes visualizar su estado en Listado de Campaas');
			this.$router.push({ name: 'home' });
		},
		onFromChange(selectedDates, dateStr, instance) {
			this.$set(this.configFechaFin, 'minDate', dateStr);
		},
		onToChange(selectedDates, dateStr, instance) {
			this.$set(this.configFechaInicio, 'maxDate', dateStr);
		},
		correctoCargarMP3(){
			this.$vs.notify({color:'success',title:'Upload Success',text:'Lorem ipsum dolor sit amet, consectetur'})
		},
		errorCargarMP3(){
			this.$vs.notify({color:'error',title:'Error Success',text:'Lorem ipsum dolor sit amet, consectetur'})
		},
		alerta(Color, titulo, texto){
			this.$vs.dialog({
				color: Color,
				title: titulo,
				text: texto,
			})
		},
		alerta2(Color, titulo, texto){
			this.$vs.dialog({
				color: Color,
				title: titulo,
				text: texto,
				accept: this.abrirExcel,
			})
		},
		abrirExcel(){
			this.modalExcel = true;
		},
		loadDataInTable ({ results, header, meta }) {
			this.header = header;
			this.tableData = results;
			this.sheetName = meta.sheetName;
			var iguales = true;

			/* 
			Formatos vlidos celular_regx:
			(123) 456-7890
			(123)456-7890
			123-456-7890
			123.456.7890
			1234567890
			+31636363634
			075-63546725
			*/
			var alfanumerico = /^[a-zA-Z0-9_]*$/;
			var celular_regx = /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/;
			var telefono_regx = /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{1,6}$/;
			if (header.length == this.validacionHeaderExcel.length){
				for (var i in header){
					if ( header[i] != this.validacionHeaderExcel[i] ){ iguales = false }
				}
				if( iguales ){
					isvalid(results, {
					type: Array,
					schema: {
						type: Object,
						schema: {
							'identidad': { required: true,
								errors: {
									required: 'Dato faltante en la columna "nombre"',
								},
								post: function(data, schema, fn) {
									if (!alfanumerico.test(data)) {
										throw new Error('Datos en la columna "identidad" debe ser numrico o alfanumrico');
									}
								}
							},
							'nombre': { type: String, required: true,
								errors: {
									type: 'Dato en la columna "nombre" que no es texto',
									required: 'Dato faltante en la columna "nombre"',
								}
							},
							'fecha_nacimiento': { type: String, required: true,
								match: /^([0-9]{2})?[-/]?([0-9]{2})?[-/]?([0-9]{4})$/,
								errors: {
									type: 'Dato en la columna "fecha_nacimiento" que no es una fecha dd-mm-aaaa o dd/mm/aaaa',
									required: 'Dato faltante en la columna "fecha_nacimiento"',
									match: 'Dato en la columna "fecha_nacimiento" no es vlido slo se permiten dd-mm-aaaa o dd/mm/aaaa',
								}
							},
							'celular': {required: true,
								errors: {
									required: 'Dato faltante en la columna "celular"',
								},
								post: function(data, schema, fn) {
									if (!celular_regx.test(data)) {
										throw new Error('Dato en la columna "celular" no es vlido debe ser +123456780 o 1234567890 de mnimo 10 dgitos');
									}
								}
							},
							'telefono': { required: true, 
								errors: {
									required: 'Dato faltante en la columna "telefono"',
								},
								post: function(data, schema, fn) {
									if (!telefono_regx.test(data)) {
										throw new Error('Dato en la columna "telfono" no es vlido debe ser +1234567 o 1234567 de mnimo 7 dgitos');
									}
								}
							},
							'email': { type: String, required: true, match: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
								errors: {
									type: 'Dato en la columna "email" que no es texto',
									required: 'Dato faltante en la columna "email"',
									match: 'Dato en la "email" no tiene el formato de un email aaaa@bbbb.ccc',
								}
							}
						}
					}}).then((data) => {
						console.log('Datos correctos');
						this.modalExcel = true;
						this.campanaNueva.cantidadUsuarias = results.length;
					}).catch((err, validData) => {
						console.log('ERROR ' + err);
						this.alerta('danger', 'Error en los datos al cargar nuevas usuarias', err);
						this.campanaNueva.cantidadUsuarias = 0;
						this.tableData = [];
						this.header = [];
					});
				}
				else{
					this.alerta('danger', 'Error al cargar nuevas usuarias', 'Recuerda que la cabecera del documento tiene que ser "identidad - nombre - fecha_nacimiento - celular - telefono - email"');
					this.campanaNueva.cantidadUsuarias = 0;
					this.tableData = [];
					this.header = [];
				}
			}else{
				this.alerta('danger', 'Error al cargar nuevas usuarias', 'Recuerda que la cabecera del documento tiene que ser "identidad - nombre - fecha_nacimiento - celular - telefono - email"');
				this.campanaNueva.cantidadUsuarias = 0;
				this.tableData = [];
				this.header = [];
			}
		},
		cancelarSubidaUsuarias(){
			this.campanaNueva.cantidadUsuarias = 0;
			this.tableData = [];
			this.header = [];
			this.campanaNueva.nombre = '';
			this.campanaNueva.nombreDB = '';
			this.modalExcel = false;
		},
		cargarSubidaUsuarias(){
			if (this.campanaNueva.nombre != '' && this.campanaNueva.nombreDB != ''){
				this.modalExcel = false;
			}else{
				this.modalExcel = false;
				this.alerta2('warning', 'Ingresar nombres', 'Debes ingresar un nombre de la campaa y un nombre a la nueva lista de usuarias a crear');
			}
		},
	},
	components: {
		FormWizard,
		TabContent,
		StatisticsCardLine,
		flatPickr,
		IconifyIcon,
		quillEditor,
		ImportExcel
	},
}
